:root {
  --active-color: #00acdc;
}

body {
  background-color: #e5e5e5;
  margin: 0;
  font-family: Roboto, serif;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

nav {
  gap: 20px;
  display: flex;
}

.nav__list {
  align-items: center;
  gap: 20px;
  margin: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.header_link {
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.header-logo {
  width: 100%;
  height: 100%;
}

.header-logo__link {
  width: 40px;
  height: 40px;
}

.header_link:hover, .is-active {
  color: var(--active-color);
}

.header_link.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__item {
  display: flex;
}
